:root {
  --hover-color: #00acdc;
}

body {
  margin: 0;
}

h1 {
  text-align: center;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font: 12px Roboto, Arial, sans-serif;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-left: 20px;
  display: flex;
}

.nav-item:first-child {
  margin: 0;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
